@import url('https://fonts.googleapis.com/css2?family=Roboto+Mono&display=swap');
@import url('https://fonts.googleapis.com/css2?family=Lobster&display=swap');

.about {
    margin-left: 121px;
    margin-top: 0;
    background-color: #1d1d1d;
    color: white;
    height: 100vh;
    width: calc(100vw - 121px);
    display: flex;
    overflow: hidden;

    .about_Title {
        padding-top: 150px;
        padding-left: 70px;

        h1 {
            position: absolute;
            top: 50px;
            color: #08fdd8;
            font-size: 56px;
            font-family: 'Lobster', cursive;
            animation-name: upToDown3;
            animation-duration: 1s;
            animation-iteration-count: 1;
            animation-fill-mode: forwards;
            animation-direction: ease-in-out;
        }

        @keyframes upToDown3 {
            from {
                top: -100px;
            }

            to {
                top: 50px;
            }
        }

        p {
            width: 361px;
            margin: 20px 0;
            font-family: 'Roboto Mono', monospace;
        }

        .color {
            color: #08fdd8;
        }
    }

    .aboutImage {
        img {
            height: 90vh;
            margin: 0vh 100px;
        }
    }
}

@media screen and (max-width: 412px) {
    .about {
        margin-top: 5vh;
        margin-left: 0;
        height: 100vh;
        display: block;
        width: 100vw;
        overflow-x: hidden;

        .about_Title {
            padding-top: 50px;
            padding-left: 20px;

            h1 {
                position: static;
            }
        }

        .aboutImage {
            img {
                height: 30vh;
                margin: 20px 60px;
            }
        }
    }
}