.sign-in-contanier {
    margin-left: 121px;
    background-color: hsl(0, 0%, 11%);
    color: white;
    height: 100vh;
    width: calc(100vw - 121px);
    padding-top: 20px;
    padding-left: 70px;
    display: flex;
    justify-content: space-around;
}

@media screen and (max-width: 412px) {
    .sign-in-contanier { 
        margin: 5vh 15px 0 15px;
        height: 130vh;
        padding: 20px 50px 0 10px;
        margin-left: 0;
        width: 100vw;
        display: block;
        justify-content: none;
    }
}