@import url('https://fonts.googleapis.com/css2?family=Roboto+Mono&display=swap');
@import url('https://fonts.googleapis.com/css2?family=Lobster&display=swap');

.mySkills {
    margin-left: 121px;
    margin-top: 0;
    background-color: #1d1d1d;
    color: white; 
    height: 100vh;
    width: calc(100vw - 121px);
    display: flex;
    overflow-x: hidden;

    .mySkills_Title {
        padding-top: 106px;
        padding-left: 70px;

        h1 {
            position: absolute;
            top: 50px;
            color: #08fdd8;
            font-size: 56px;
            font-family: 'Lobster', cursive;
            animation-name: upToDown;
            animation-duration: 1s;
            animation-iteration-count: 1;
            animation-fill-mode: forwards;
            animation-direction: ease-in-out;
        }

        @keyframes upToDown {
            from {
                top: -50px;
            }

            to {
                top: 50px;
            }
        }

        p {
            width: 361px;
            margin: 50px 0 10px 0;
            font-family: 'Roboto Mono', monospace;
        }

        .color {
            color: #08fdd8;
        }
    }

    .ProgreCard {
        .cardContainer {
            display: flex;

            .card {
                background-color: #2a2a2a;
                color: white;
                height: 34vh;
                width: 19vw;
                margin-right: 3rem;
                position: static;
                margin-left: 3rem;

                .card-body {
                    .card-title {
                        padding-bottom: 10px;
                    }

                    .card-subtitle {
                        margin-bottom: 10px;
                        color: #ababab;
                    }

                    .card-text {
                        color: #ababab;
                    }
                }
            }
        }
    }
}

@media screen and (max-width: 412px) {
    .mySkills {
        margin-left: 0;
        margin-top: 5vh;
        height: 100vh;
        width: 100vw;
        display: block;
        overflow-x: hidden;

        .mySkills_Title {
            padding-left: 20px;
            padding-top: 50px;

            h1 {
                position: static;
            }
        }

        .ProgreCard {
            .cardContainer {
                .card {
                    margin-right: 2vh;
                    margin-left: 2vh;
                    margin-bottom: 10vh;
                    padding: 0;
                    height: 43vh;
                    width: 40vw;

                    .card-body {
                        .card-title {
                            padding-bottom: 5px;
                        }
                    }

                    .card-subtitle {
                        margin-bottom: 5px;
                    }
                }
            }
        }
    }
}