.progresLine {
    padding-top: 130px;
    padding-left: 70px;

    .progresLine_container {
        margin: 20px 10px;

        span {
            font-family: 'Lobster', cursive;
            font-size: 20px;
        }

        .line {
            width: 40vw;
            height: 4px;
            border-radius: 10px;
            border: 1px solid #08fdd8;

            .fillUp {
                width: 35vw;
                height: 3px;
                background-color: #08fdd8;
                border-radius: 10px;
                animation-name: line1;
                animation-duration: 5s;
                animation-iteration-count: 1;
                animation-fill-mode: forwards;
            }

            @keyframes line1 {
                from {
                    width: 0px;
                }

                to {
                    width: 35vw;
                }
            }
        }

        .cl2 {
            border: 1px solid rgb(255, 34, 83);

            .fillUp {
                background-color: rgb(255, 34, 83);
                width: 30vw;
                animation-name: line2;
                animation-duration: 5s;
                animation-iteration-count: 1;
                animation-fill-mode: forwards;
            }

            @keyframes line2 {
                from {
                    width: 0px;
                }

                to {
                    width: 30vw;
                }
            }
        }

        .cl3 {
            border: 1px solid rgb(210, 108, 213);

            .fillUp {
                background-color: rgb(210, 108, 213);
                width: 25vw;
                animation-name: line3;
                animation-duration: 5s;
                animation-iteration-count: 1;
                animation-fill-mode: forwards;
            }

            @keyframes line3 {
                from {
                    width: 0px;
                }

                to {
                    width: 25vw;
                }
            }
        }
    }
}

@media screen and (max-width: 412px) {
    .progresLine {
        padding-top: 50px;
        padding-left: 20px;
        width: 100vw;

        .progresLine_container {
            margin: 20px 10px;

            .line {
                width: 80vw;
                height: 4px;

                .fillUp {
                    width: 70vw;
                }

                @keyframes line1 {
                    from {
                        width: 0px;
                    }

                    to {
                        width: 70vw;
                    }
                }
            }

            .cl2 {
                .fillUp {
                    width: 60vw;
                }

                @keyframes line2 {
                    from {
                        width: 0px;
                    }

                    to {
                        width: 60vw;
                    }
                }
            }

            .cl3 {

                .fillUp {
                    width: 50vw;
                }

                @keyframes line3 {
                    from {
                        width: 0px;
                    }

                    to {
                        width: 50vw;
                    }
                }
            }
        }
    }
}