@import url('https://fonts.googleapis.com/css2?family=Lobster&display=swap');
@import url('https://fonts.googleapis.com/css2?family=Roboto+Mono&display=swap');

.form-box {
    width: 400px;
    height: 500px;
    background: transparent;
    border: 2px solid #08fdd8;
    border-radius: 20px;
    backdrop-filter: blur(15px);
    display: flex;
    justify-content: center;
    align-items: center;


    .form-value {
        form {
            h2 {
                font-size: 2rem;
                color: white;
                text-align: center;
                font-family: 'Lobster', cursive;
            }

            .inputbox {
                position: relative;
                margin: 30px 0;
                width: 310px;

                input:focus~label,
                input:valid~label {
                    top: -5px;
                }

                input {
                    width: 100%;
                    height: 50px;
                    background: transparent;
                    border: none;
                    outline: none;
                    font-size: 1em;
                    padding: 0 35px 0 5px;
                    color: #fff;
                }

                .fillUp {
                    width: 100%;
                    height: 3px;
                    background-color: #08fdd8;
                    border-radius: 10px;
                    animation-name: line2;
                    animation-duration: 5s;
                    animation-iteration-count: 1;
                    animation-fill-mode: forwards;
                }

                @keyframes line2 {
                    from {
                        width: 0px;
                    }

                    to {
                        width: 100%;
                    }
                }

                ion-icon {
                    position: absolute;
                    right: 8px;
                    color: #ababab;
                    font-size: 1.2em;
                    top: 20px;
                }

                label {
                    position: absolute;
                    top: 50%;
                    left: 5px;
                    transform: translateY(-50%);
                    color: #ababab;
                    font-size: 1em;
                    pointer-events: none;
                    transition: .5s;
                    font-family: 'Roboto Mono', monospace;
                }
            }

            button {
                margin: 20px 0;
                display: block;
                width: 100%;
                height: 40px;
                border-radius: 40px;
                background: #08fdd8;
                outline: none;
                cursor: pointer;
                font-size: 1em;
                font-weight: bold;
                border: 2px solid #08fdd8;
            }

            button:hover {
                background: #1d1d1d;
                color: white;
            }
        }
    }
}

@media screen and (max-width: 412px) {

    .form-box {
        width: 387px;
        margin-top: 50px;
        backdrop-filter: none;

        .form-value {
            form {

                .inputbox {
                    position: static;
                    margin: -10px 0;

                    ion-icon {
                        position: static;
                        right: 0;
                        top: 0;
                        color: #ababab;
                        font-size: 1.2em;
                        visibility: hidden;
                    }

                    label {
                        position: static;
                        top: 0px;
                        left: 0px;
                        color: #ababab;
                        font-size: 1em;
                        transform: none;
                        transition: none;
                        font-family: 'Roboto Mono', monospace;
                    }
                }
            }
        }
    }
}