@import url('https://fonts.googleapis.com/css2?family=Lobster&display=swap');

.menu {
    height: 100vh;
    width: 121px;
    background-color: black;
    position: fixed;
    top: 0px;
    left: -125px;

    .icon-container {
        ion-icon {
            color: #ababab;
            font-size: 45px;
            position: fixed;
            visibility: hidden;
        }
    }


    .menu_Top {
        padding: 13vh 0;

        img {
            height: 127px;
            width: 121px;
            margin: auto;
        }

        span {
            font-family: 'Lobster', cursive;
            font-size: 26px;
            color: white;
            margin: 12px;
        }
    }

    .menu_bottom {
        ul {
            li {
                margin: 10px 0;

                a {
                    font-weight: bolder;
                    color: #909096;
                }

                a:hover {
                    color: #08fdd8;
                }

            }
        }
    }
}

@media screen and (max-width: 412px) {
    .menu {

        .icon-container {
            width: 100vw;
            position: fixed;
            right: 0;
            background-color: black;
            top: 0;
            height: 5vh;

            ion-icon {
                visibility: inherit;
            }
        }

        .menu_Top {
            padding: 45px 0px 70px 0;
        }

        .menu_bottom {
            ul {
                li {
                    margin: 20px 0;

                    a {
                        font-size: 20px;
                    }
                }
            }
        }
    }
}