@import url('https://fonts.googleapis.com/css2?family=Lobster&display=swap');

.home {
    margin-left: 121px;
    margin-top: 0;
    background-color: #1d1d1d;
    color: white;
    height: 100vh;
    width: calc(100vw - 121px);
    display: flex;
    overflow: hidden;

    .intro_heading {
        padding-top: 80px;

        span {
            font-family: 'Lobster', cursive;
            display: block;
            font-size: 70px;
            font-weight: bolder;
            margin: 0px 30px;
        }

        span:hover {
            color: #08fdd8;
        }

        .but {
            margin: 50px;
            background-color: #1d1d1d;
            color: #08fdd8;
            font-weight: bolder;
            font-size: 17px;
            padding: 5px 20px;
            border: 2px solid #08fdd8;
            border-radius: 10px;
        }

        .but:hover {
            background-color: #08fdd8;
            color: #1d1d1d;
        }

        .but:-webkit-any-link {
            text-decoration: none;
        }
    }

    .image {
        img {
            height: 75vh;
            margin: 10vh 30px;
        }
    }
}

@media screen and (max-width: 412px) {
    .home {
        margin-top: 5vh;
        margin-left: 0;
        height: 100vh;
        width: 100vw;
        display: block;
        overflow-x: hidden;

        .intro_heading {
            padding-top: 50px;
        }

        .image {
            img {
                height: 30vh;
                margin-top: 20px;
                margin-left: 20px;
                margin-bottom: 30px;
            }
        }
    }
}